import config from '../config.js';
import MessageParser from '../Messageparser.js';
import ActionProvider from '../ActionProvider.js';
import Chatbot from 'react-chatbot-kit';
import Modal from 'react-modal';
import React from 'react'

import './bot.css'
import './chatroom.css';
import {Link} from "react-router-dom";


function ChatBott() {
    let subtitle;
    const [modalIsOpen, setIsOpen] = React.useState(false);
    
    function openModal() {
      setIsOpen(true);
    }
    
    function afterOpenModal() {
      // references are now sync'd and can be accessed.
      subtitle.style.color = '#f00';
    }
    
    function closeModal() {
      setIsOpen(false);
    }
    const customStyles = {
      overlay: {
        position: 'fixed',
        zIndex: 2,
        backgroundColor: '#000',
       
        
      },
      content: {
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        position: 'fixed',
        background: '#000',
        color:'white',
        overflow:'hidden',
        outline: 'none',
        width:'100%',
        height:'100%',
        border:'0px',
        padding:'0px',
        margin:'0px',
      },
      };
    return (
    <><div class="chatroom">
    <div class="left">
       <div class="header">
        <div class="logo">
            <img src="/Assets/idk.png" alt=""/>
            </div>
            <div class="links">
                <div class="text"><h6><strong>Chat to</strong></h6><h6>name</h6></div>
                <div class="link_btns">
                    <button class="bg-blue">Allments</button>
                    <button class="bg-blue">Medication Review</button>
                    <button class="bg-blue">Pom-P</button>
                </div>
            </div>
       </div>
       <div>
      <Chatbot
        config={config}
        messageParser={MessageParser}
        actionProvider={ActionProvider}
        bubbleStyle={{ fontSize: '5px' }}
        enableSmoothScroll={true}
      />
    </div>
    </div>
    <div class="right">
        <div class="img">
            <img src="/Assets/chat_img.png" alt=""/>
            <h4>name</h4>
            <h5>Lorem, ipsum dolor.</h5>
        </div>
        <button class="bg-skyblue" onClick={openModal}>Stuck?</button>
        <br/>
        <button>View Rx</button>
        <div class="med">
            <h4>Medication List</h4>
            <span>
                bisoprolol<br/>
                atenolol <br/>
                ramipril  <br/>
                paracetamol
            </span>
            
        </div>
        
            <button class="bg-skyblue"><Link to='/endConsultant'>End Consultation</Link></button>
            <br/>
        <button>View Reference Range</button>
            
    </div>
</div>



<Modal 
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        onRequestClose={closeModal}
        style={customStyles}
       
        
      >
        <div className='orbit__modal-content'>
         <div className='modal-header'>
            
        
        <button onClick={closeModal}>close</button>
        
        <div class="modal_body">
            <div class="back-btn">
                <img src="/Assets/back.svg" alt="" />
            </div>
            <div class="heading">
                <h3>Hints board</h3>
            </div>

            <div class="round sky">

                <p>
                    Think about the presenting symptoms <br />
                    Why might the patiant be experiencing breathlessness <br />
                </p>
            </div>
            <div class="round sky">

                <p>
                    Think about the presenting symptoms <br />
                    Why might the patiant be experiencing breathlessness <br /> </p>
            </div>
            <div class="round sky">

                <p>
                    Think about the presenting symptoms <br />
                    Why might the patiant be experiencing breathlessness <br /> </p>
            </div>
            <div class="round sky">

                <p>
                    Think about the presenting symptoms <br />
                    Why might the patiant be experiencing breathlessness <br /> </p>
            </div>
        </div>
        </div>
        </div>
      </Modal>
</>

    );
}

export default ChatBott;