import React from 'react';
import './login.css';
import imgDesign from "../images/Untitled design.png";
import imgBackBtn from "../images/backbtn.svg";
import imgLogin from "../images/login.png";
import {Link} from "react-router-dom";
function LoginPage() {
    return ( 
        <>  <div class="login">
        <div class="login-img">
            <div class="logo">
                <img src={imgDesign} width="150px" alt="no img"/> 
            </div>
        <div class="img">
            <img src={imgLogin}   alt="no img"/>
        </div>
    </div>
       <div class="login-Form">
        <div class="header">
            <div class="back"><img src={imgBackBtn} width="30px" height="20px" alt="no img"/></div>

            <span>Welcome to I- Diagknows</span>
        </div>
        <div class="text-area">
            <h3>Welcome to I- Diagknows</h3>
            <div class="input">
            <input type="email" name="email" id="email" placeholder="Email" />
            <br/>
            <input type="password" name="password" id="password" placeholder="Pawsord" />
            <br/>
            <div class="right"><button class="bills bg-blue"><Link to="/get_started">LOGIN</Link></button></div>
        </div>
        
            <div class="log">
                <div class="log-img">
                    <img src="" alt=""/>
                </div>
            </div>
        </div>
        <div class="footer">
            <span>Dont Have An Account?. <Link to="/signUp">Signup</Link></span>
        </div>
       </div>
    </div></>
     );
}

export default LoginPage;