import React from 'react';
import './login.css';
import imgDesign from "../images/Untitled design.png";
import imgBackBtn from "../images/backbtn.svg";
import {Link} from "react-router-dom";
import imgLogin from "../images/login.png";
function SignUpPage() {
    return ( <>
    <div class="login">
        <div class="login-img">
            <div class="logo">
                <img src={imgDesign} width="150px" alt="no img"/> 
            </div>
        <div class="img">
            <img src={imgLogin}   alt="no img"/>
        </div>
    </div>
       <div class="login-Form">
        <div class="header">
            <div class="back"><img src={imgBackBtn} width="30px" height="20px" alt="no img"/></div>
    
            
        </div>
        <div class="text-area">
            <h3>Welcome to I- Diagknows</h3>
            <div class="input">
            <input type="text" name="" id="email" placeholder=" &nbsp;&nbsp;Name" />
            <br/>
            <input type="email" name="email" id="email" placeholder="&nbsp;&nbsp;Email" />
            <br/>
            <input type="email" name="" id="" placeholder="&nbsp;&nbsp;Password" />
            <br/>
            <input type="password" name="password" id="password" placeholder="&nbsp;&nbsp;Confirm Pawsord" />
            <br/>
            <div class="right"><button class="bills bg-blue">SIGNUP</button></div>
        </div>
        
            <div class="log">
                <div class="log-img">
                    <img src="" alt=""/>
                </div>
            </div>
        </div>
        <div class="footer">
            <span>Already Have An Account? <Link to="/login">login Now</Link></span>
        </div>
        </div>
       </div></> );
}

export default SignUpPage;