import React from "react";
import './home.css'
import img1 from '../images/Untitled design.png';
import imgFb from "../images/facebook.svg";
import imgG from "../images/googleplus.svg";
import imgT from "../images/twitter.svg";
import imgI from "../images/instagram.svg";
import imgC from "../images/computer.png"
import imgM from "../images/mouse.svg";
import imgL from "../images/Layer.png";
import {Link} from "react-router-dom";
import Result from "./result";
import Contact from "./contact";
function HomePage() {
    return ( <> 
      <div class="Home">
        <div class="nav">
            <div class="home-logo"><img src={img1} height="80px" width="130px" alt="no img"/>
              
            </div>
            <div class="home-nav">
                <ul>
                    <li><a class="active" href="">Home</a></li>
                    <li><a class="Result" href="">Result</a></li>
                    <li><a class="Contact Us" href="">Contact Us</a></li>
                </ul>
                <div class="login">
                    <span class="login"><Link to="/login">LOGIN</Link> </span>
                </div>
            </div>
        </div>
        <div class="home-main">
            <div class="home-links">
                <img src={imgFb} width="20px" alt="no img"/>
                <img src={imgG} width="30px" alt="no img"/>
                <img src={imgT} width="30px" alt="no img"/>
                <img src={imgI} width="30px" alt=""/>
            </div>
            <div class="home-text">
                <h3>CONSULTATION SKILLS</h3>
                <h1>FOR HEALTHCARE</h1>
                <p>PERFORMANCE BASED MEDICATIONS AND CONSULTING<br/>WE ARE LOREM MEDIA</p>
                <button class="pill bg-blue">Get Started</button>
            </div>
            <div class="home-img">
                <img src={imgC} width="380px" height="280px" alt="no img"/>
    
            </div>
        </div>
        <div class="home-center">
            <div class="center-home">
                <img src={imgM} alt="no img"/>
            </div>
        </div>
        <div class="home-layer">
            <div class="layer-img"><img src={imgL} width="500px" height="400px" alt="no img"/></div>
            <div class="layer-text">
                <h4>Biography</h4>
                <h1>Who We Are</h1>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Voluptatum, ipsum. Vero enim inventore
                    minima est eos fugiat sequi iste neque temporibus esse distinctio vel magnam maxime, dolorum, natus
                    perferendis adipisci. <br/><br/> Lorem ipsum dolor sit amet consectetur adipisicing elit. <br/><br/> Lorem ipsum dolor sit amet consectetur adipisicing elit.</p>
                    <button class="pills bg-blue">SEE MORE</button>
            </div>
        </div>
    
    </div>
    <Result/>
    <Contact/>
    
    </> );
}

export default HomePage;